import React, { useEffect, useState } from 'react';
import * as moment from 'moment';
import {
  Legend, Pie, PieChart, ResponsiveContainer,
} from 'recharts';
import useRemote from '../../utils/useRemote';
import Row from '../../widgets/Row';
import Column from '../../widgets/Column';
import DateInput from '../../widgets/DateInput';
import SelectEditor from '../../inputs/SelectEditor';

const Stats = [
  {
    label: 'Categorie pi vendute',
    resource: 'categories-amount',
  },
  {
    label: 'Categorie pi redditizie',
    resource: 'categories-profit',
  },
  {
    label: 'Piatti pi venduti',
    resource: 'dishes-amount',
  },
  {
    label: 'Piatti pi redditizi',
    resource: 'dishes-profit',
  },
];

export default function StatisticsPage() {
  const colors = ['#AC80A0', '#89AAE6', '#3685B5', '#E471A6', '#061826'];

  const [from, setFrom] = useState(moment()
    .add(-1, 'months'));
  const [to, setTo] = useState(moment());

  const [option, setOption] = useState(Stats[0]);

  const [data, refresh] = useRemote(`statistics/${option.resource}/${from.format('YYYY-MM-DD')}/${to.format('YYYY-MM-DD')}`, {
    auto: false,
  });

  useEffect(() => {
    refresh();
  }, [refresh]);

  let stats;
  if (data) {
    stats = {
      ...data,
      entries: data.entries.map((entry, i) => ({
        ...entry,
        fill: colors[i],
      })),
    };
  }

  return (
    <Column grow>
      <Row spaced justifyCenter alignCenter>
        <Column grow spaced justifyStretch>
          <DateInput label="Da" date={from} onDateChange={date => setFrom(date)} />
          <DateInput label="A" date={to} onDateChange={date => setTo(date)} />
        </Column>
        <Column grow>
          <SelectEditor
            label="Statistica"
            value={option}
            id={opt => opt.resource}
            text={opt => opt.label}
            options={Stats}
            onSelectOption={opt => setOption(opt)}
          />
          {data
          && (
            <p className="has-text-centered">
              <strong>
              Totale:
                {' '}
                {data.entries.map(entry => entry.value)
                  .reduce((a, b) => a + b, 0)}
              </strong>
            </p>
          )}
        </Column>
      </Row>
      {stats
      && (
        <Row grow justifyCenter>
          <ResponsiveContainer>
            <PieChart>
              <Pie
                isAnimationActive={false}
                nameKey="name"
                dataKey="value"
                data={stats.entries}
                label
              />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        </Row>
      )
      }
    </Column>
  );
}
