import React from 'react';
import Row from '../../../../../widgets/Row';
import Column from '../../../../../widgets/Column';
import Button from '../../../../../widgets/Button';
import {
  faCaretLeft,
  faCaretRight,
  faFastBackward,
  faFastForward,
  faStepBackward,
  faStepForward
} from '@fortawesome/free-solid-svg-icons';
import Scrollable from '../../../../../widgets/Scrollable';


export default function SplitModule({
  items,
  f,
  ff,
  fff,
  b,
  bb,
  bbb,
  display
}) {
  return (
    <Row grow>
      <Column>
        <Row grow>
          <Column>
            <Scrollable>
              {items.map(item => (
                <Row dense>
                  {(b || bb) && (
                    <Column size="1">
                      <Row>
                        <Column>
                          <Button
                            icon={faStepBackward}
                            onClick={() => bb(item)}
                          />
                        </Column>
                        <Column>
                          <Button
                            icon={faCaretLeft}
                            onClick={() => b(item)}
                          />
                        </Column>
                      </Row>
                    </Column>
                  )}
                  <Column size="3">
                    {display(item)}
                  </Column>
                  {(f || ff) && (
                    <Column size="1">
                      <Row>
                        <Column>
                          <Button
                            icon={faCaretRight}
                            onClick={() => f(item)}
                          />
                        </Column>
                        <Column>
                          <Button
                            icon={faStepForward}
                            onClick={() => ff(item)}
                          />
                        </Column>
                      </Row>
                    </Column>
                  )}
                </Row>
              ))}
            </Scrollable>
          </Column>
        </Row>
        <Row>
          {bbb && (
            <Column size="1">
              <Button icon={faFastBackward} onClick={() => bbb()}/>
            </Column>
          )}
          <Column size="3"/>
          {fff && (
            <Column size="1">
              <Button icon={faFastForward} onClick={() => fff()}/>
            </Column>
          )}
        </Row>
      </Column>
    </Row>
  );
}
